[class$=__form-control]:invalid {
	border-color: #d08181;
	outline-color: #d08181;
	box-shadow: 0 0 3px rgba(208, 129, 129, 0.4);
}

[class$=__form-control]:valid {
	border-color: #93c273;
	outline-color: #93c273;
	box-shadow: 0 0 3px rgba(147, 194, 115, 0.4);
}

.authorization__form:invalid .authorization__submit {
	pointer-events: none;
	background-color: #ccc;
	border-color: #ccc;
	border-bottom-color: #b8b8b8;
}
